// Tablet styles
@include for-size(tablet-portrait-up) {

  .navbar {
    & > div {
      button {
        font-size: 1rem; } } }

  #cover {
    figure {
      width: 80vw;
      height: 100%;
      top: 0;
      margin-top: 0;
      background-position: 5vw -160%;
      background-size: 130% auto; }
    h2 {
      font-size: 8.5vw; } }

  #about {
    & > p {
      font-size: 1rem;
      line-height: 1.3; }

    .button-expand-popup {
      width: 19vw;
      height: 9vw;
      font-size: 0.9rem; }

    .popup {
      p {
        font-size: 1rem; }
      &.is-active {
        height: 100%; } } }

  #features {
    article {
      h2 {
        width: 30vw; }
      p {
        font-size: 1rem; } } }

  #benefits {
    ul {
      width: 45vw;
      h4 {
        font-size: 1rem;
        width: auto; } }
    figure {
      height: 68vw; } }

  #tweets {
    strong {
      font: 1rem $font-title; } }

  .form-newsletter {
    width: 35vw; }

  #contact {
    figure {
      width: 40vw;

      &.is-active {
        height: 50vw; } }

    .button-expand-popup {
      width: 19vw;
      height: 9vw;
      font-size: 0.9rem;

      span {
        width: auto; } } }

  .menu {
    height: 70vh;
    &.menu:last-of-type {
      margin-top: 5vw;
      margin-bottom: 10vw; }

    ol {
      flex: 0 1 60%;
      padding: 5vw 0 0 10vw;
      font: 6vw $font-title;
      align-self: flex-end; }

    li {
      margin-bottom: 5vw; } }


  #form-demo {
    form {
      flex: 0 0 82%;
      padding: 50px 15%; }

    input {
      &:not([type="submit"]) {
        height: 55px; }

      &[type="submit"] {
        height: 55px; } }

    .close {
      width: 55px;
      height: 55px; } } }
