// Capa
#cover {
  height: 110vh;
  position: relative;
  overflow: hidden;

  // video
  &::before {
    content: '';
    width: 100vw;
    height: 100%;
    background: {
      image: url('../img/lion.jpg');
      repeat: no-repeat;
      size: cover; }
    position: absolute;
    top: 0;
    left: 0;
    mix-blend-mode: overlay; }

  // X
  figure {
    width: 60vw;
    height: 100vw;
    overflow: hidden;
    background: {
      image: url('../img/cover-x.svg');
      repeat: no-repeat;
      position: 5vw center;
      size: 100% auto; }
    position: absolute;
    top: 50%;
    right: 0;
    margin-top: -68%;
    z-index: 1;
    mix-blend-mode: color-dodge;
    transform-style: preserve-3d;
    transform: translateZ(20px); }

  div {
    position: absolute;
    top: 50%;
    left: $default-margin;
    transform: translateY(-50%);
    width: 80%;
    z-index: 2;

    // arrow down
    &::after {
      content: '';
      width: 40px;
      height: 60px;
      background: url('../img/arrow-down.svg') no-repeat center center {
        size: contain; }
      position: absolute;
      left: 0;
      bottom: -140px;
      animation: scroll-down 0.5s ease 0.2s infinite alternate; } }

  @keyframes scroll-down {
    from {
      bottom: -140px; }
    to {
      bottom: -150px; } }

  h2 {
    font: {
      family: $font-title;
      size: 4.5vw; }
    margin-bottom: 60px; }

  a {
    @extend %button-demo; } }
