// Tweets
#tweets {
  margin-top: 25vw;
  position: relative;
  display: flex;
  overflow-x: hidden;

  &::after {
    content: '';
    width: 35vw;
    height: 100%;
    background-color: $color-beige;
    position: absolute;
    right: 0;
    top: 0; }

  strong {
    flex: 0 0 26.5vw;
    box-sizing: border-box;
    color: $color-dark;
    font: 1.5vw $font-title;
    padding: 40px 8vw 40px $default-margin;
    position: relative;
    z-index: 1; }

  .carousel {
    flex: 0 0 auto;
    position: relative;
    z-index: 1;

    &::after {
      content: '';
      width: 10px;
      height: 8vw;
      background-color: $color-green;
      position: absolute;
      top: 20px;
      left: -10px; }

    button {
      position: absolute;
      width: 50px;
      height: 50px;
      left: -50px;
      overflow: hidden;
      text-indent: -999px;
      transition: all 0.5s $ease;
      background: {
        repeat: no-repeat;
        size: 6px auto;
        position: center 50%; }

      &.slick-prev {
        top: 10vw;
        background: {
          image: url('../img/arrow-prev.svg'); }
        &:hover {
          background-position: 40% center; } }
      &.slick-next {
        top: calc(10vw + 50px);
        box-shadow: -2px 2px 2px rgba(0, 0, 0, 0.1);
        background: {
          image: url('../img/arrow-next.svg');
          color: $color-green; }
        &:hover {
          background: {
            position: 60% center;
            color: lighten($color-green, 10%); } } } }


    .slick-slide {
      width: 280px !important;
      margin-right: 10px; }

    .twitter-tweet {
      margin: 0 !important; } } }
