// Big desktop styles
@include for-size(big-desktop-up) {

  #lionx {
    #navbar {
      &::before {
        height: 6.5vw; }

      figure {
        transform: scale(1.8) translate(23%, 23%); }

      & > div {
        width: 20vw;
        padding-top: 2vw;
        margin-right: 8%;

        button {
          transform: scale(1.8); } } }


    .menu {
      & > div {
        a[title*="demo"] {
          font-size: 1.2vw; }

        a[title*="LinkedIn"] {
          width: 75px;
          height: 75px; }

        address {
          font-size: 1.4vw; }

        small {
          font-size: 1rem; } } }



    #cover {
      div {
        &::after {
          width: 70px;
          height: 90px; } }
      a {
        font-size: 1.5vw; } }


    #about {
      .button-expand-popup {
        font-size: 1.3vw; } }


    #features {
      article {
        h2 {
          width: 30vw; }
        a {
          font-size: 1.5vw; } } }


    #benefits {
      ul {
        li {
          &::before {
            width: 50px;
            height: 50px;
            border: solid 11px $color-green; } } } }


    #tweets {
      .carousel {
        .slick-slide {
          width: 480px !important; }

        button {
          height: 90px;
          width: 70px;
          left: -70px;
          background-size: 10px auto;

          &.slick-next {
            top: calc(10vw + 90px); } } } }


    #contact {
      .button-expand-popup {
        font-size: 1.3vw;

        span {
          width: auto; } }

      figcaption {
        font-size: 1.4rem; } }


    .form-newsletter {
      padding-top: 100px;
      background-size: 80px auto;

      input {
        height: 70px;
        border: solid 2px $color-dark;

        &:not(:last-of-type) {
          font-size: 1.2rem; }

        &:nth-of-type(2) {
          margin-top: -2px;
          padding-right: 120px; }

        &[type="submit"] {
          width: 100px; } } }


    #contact-popup {
      input {
        &:not([type="submit"]) {
          max-width: 480px;
          font-size: 1.4rem;
          height: 65px; }

        &[type="tel"] {
          max-width: 380px; }

        &[type="submit"] {
          background-size: 25px auto;
          padding: 18px 70px 18px 36px;
          font-size: 1.4rem; } }

      textarea {
        max-width: 480px;
        font-size: 1.4rem; } } } }



