// Header
.navbar {
  width: 100%;//99.5vw
  height: 70px;
  padding: 10px 0 5px;
  position: fixed;
  top: 0;
  left: 0;
  box-sizing: border-box;
  z-index: 7;
  transition: all 0.5s $ease;

  &::before {
    content: '';
    width: 100%;
    height: 85px;
    background-color: $color-dark;
    position: absolute;
    top: -120px;
    left: 0;
    transition: all 0.9s $ease;
    opacity: 0.97; }

  figure {
    margin-left: 5%;
    display: inline-block;
    position: relative;
    z-index: 1; }

  & > div {
    float: right;
    margin-right: 5%;
    width: 280px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    padding-top: 10px;

    button {
      color: $color-light;
      font: {
        family: $font-title;
        size: 0.8rem; }
      padding: {
        top: 5px;
        bottom: 5px; }
      position: relative;
      transition: all 0.9s $ease;

      // Menu button
      &:first-child {
        padding-left: 56px;

        &::before,
        &::after {
          content: '';
          width: 40px;
          height: 1px;
          background-color: $color-light;
          position: absolute;
          left: 0;
          top: 50%;
          transition: all 0.5s $ease; }

        &::before {
          transform: translateY(-5px); }
        &::after {
          transform: translateY(5px); }

        &.close {
          &::before,
          &::after {
            width: 25px; }
          &::before {
            transform: rotate(-45deg); }
          &::after {
            transform: rotate(45deg); } } }

      // Login button
      &:last-child {
        padding-left: 35px;
        background: {
          image: url('../img/user-green.svg');
          repeat: no-repeat;
          position: left center;
          size: 17px auto; } }

      &:hover span::after {
        width: 100%; } }

    span {
      display: block;
      position: relative;

      &::after {
        content: '';
        width: 0;
        height: 1px;
        background-color: $color-light;
        position: absolute;
        bottom: -5px;
        left: 0;
        transition: all 0.3s $ease; } } }


  &.scrolled {
    &::before {
      top: 0; } }

  &.is-active {
    top: 0 !important;

    &::before {
      opacity: 0; }

    & > div {
      button {
        color: $color-dark;
        &:first-child {
          &::before,
          &::after {
            background-color: $color-dark; } }
        &:last-child {
          background-image: url('../img/user-black.svg'); }

        span::after {
          background-color: $color-dark; } } } } }
