// Características
#features {

  ol {
    margin: {
      left: $default-margin;
      top: -15vw; }
    display: flex;
    position: relative;
    z-index: 1;

    &::after {
      content: '';
      width: 35vw;
      height: 20vw;
      background-color: $color-beige;
      position: absolute;
      right: 0;
      bottom: 8vw;
      z-index: -1; }

    li {
      width: 23vw;
      height: 23vw;
      box-sizing: border-box;
      transition: all 0.5s $ease;
      position: relative;
      background: {
        repeat: no-repeat;
        size: auto 105%; }

      &:nth-child(1) {
        background: {
          image: url('../img/feature-1.jpg');
          position: 16% center; } }

      &:nth-child(2) {
        background: {
          image: url('../img/feature-2.jpg');
          position: left center; } }

      &:nth-child(3) {
        background: {
          image: url('../img/feature-3.jpg');
          position: left center; } }

      &::before {
        content: '';
        width: 1.4vw;
        height: 1.4vw;
        background-color: $color-green;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1; }

      // numeros
      &::after {
        color: $color-green;
        font: {
          size: 2.3vw;
          family: $font-title; }
        position: absolute;
        bottom: 8vw;
        left: 3vw;
        z-index: 1;
        transition: all 0.5s $ease;
        opacity: 0.6; }

      $features: 1 2 3;
      @each $number in $features {
        &:nth-child(#{$number})::after {
          content: '#{$number}'; } }

      &.is-active {
        transform: scale(1.2);
        transform-origin: center left;
        margin-right: 4.5vw;
        box-shadow: -2vw 2vw 20px rgba(0, 0, 0, 0.2);

        &::after {
          opacity: 1; }

        button {
          &::after {
            opacity: 1;
            height: 50%; }
          span {
            opacity: 1; } }

        &:hover {
          background-size: auto 105%; } }

      &:hover {
        background-size: auto 110%;
        span,
        &::after {
          opacity: 1; } } }

    button {
      width: 100%;
      height: 100%;
      position: relative;
      display: inline-flex;
      align-items: flex-end;

      &::before {
        content: '';
        width: 100%;
        height: 80%;
        background: rgb(0,0,0);
        background: linear-gradient(3deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 100%);
        position: absolute;
        left: 0;
        bottom: 0;
        opacity: 0.85; }

      &::after {
        content: '';
        width: 1.4vw;
        height: 1%;
        background: $color-green;
        position: absolute;
        left: 0;
        bottom: -3vw;
        z-index: 1;
        opacity: 0;
        transition: all 0.7s $ease; } }

    span {
      position: relative;
      z-index: 1;
      color: $color-light;
      font-size: 2.4vw;
      margin: 0 0 4vw 3vw;
      transition: all 0.5s $ease;
      opacity: 0.6; } }


  article {
    display: none;
    padding: 0 12vw;
    margin: 10vw 0 23vw;
    position: relative;

    &::before {
      content: '';
      width: 35vw;
      height: 60vw;
      background: {
        repeat: no-repeat;
        size: cover; }
      position: absolute;
      right: 0;
      top: -18vw;
      opacity: 0.4;
      mix-blend-mode: luminosity; }

    &::after {
      content: '';
      width: 7vw;
      height: 14px;
      background-color: $color-green;
      position: absolute;
      right: 28vw;
      top: 42vw; }

    &.tab-content {
      display: none; }

    &.is-active {
      display: block; }

    &.tab-plataforma::before {
      background: {
        image: url('../img/feature-1.jpg');
        position: 25% center; } }
    &.tab-analytics::before {
      background: {
        image: url('../img/feature-2.jpg');
        position: 25% center; } }
    &.tab-ia::before {
      background: {
        image: url('../img/feature-3.jpg');
        position: 25% center; } }


    h2 {
      width: 20vw;
      font: {
        family: $font-title;
        size: 2vw; }
      line-height: 1.4;
      position: relative;
      z-index: 1; }

    p {
      width: 30vw;
      margin: 4.5vw 0 3vw;
      font: {
        weight: 300;
        size: 1.7vw; }
      line-height: 1.6;
      position: relative;
      z-index: 1; }

    em {
      color: #99ff40; }

    a {
      font: {
        size: 0.8rem;
        family: $font-title; }
      text-decoration: underline;
      color: $color-light;
      transition: all 0.5s $ease;
      position: relative;
      z-index: 1;

      &:hover {
        color: #99ff40; } }

    .features-list {
      width: 40vw;
      position: absolute;
      top: 1vw;
      right: 18.2vw;
      transition: all 0.5s ease;

      &::after {
        content: '';
        width: 73vw;
        height: 15vw;
        background-color: $color-green;
        position: absolute;
        right: 0;
        top: 8vw;
        mix-blend-mode: overlay; } }

    .feature-item {
      text-align: right;
      outline: none; }

    h3 {
      font: {
        family: $font-title;
        size: 2vw; }
      color: $color-green;
      text-transform: uppercase;
      vertical-align: bottom;
      display: inline-block;
      border-bottom: solid 1vw $color-green;
      padding-bottom: 2vw; } } }
