// Demo
#demo {
  position: relative;
  width: 100%;
  height: 35vw;
  margin-top: 15vw;

  &::before {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: url('../img/charts.jpg') no-repeat center center {
      size: cover; }
    mix-blend-mode: screen;
    opacity: 0.75; }

  &::after {
      content: '';
      width: 40vw;
      height: 60vw;
      background: url('../img/cover-x.svg') no-repeat 0 0 {
        size: contain; }
      position: absolute;
      top: -15vw;
      left: 0;
      mix-blend-mode: screen;
      opacity: 0.15; }

  small {
    display: block;
    width: 44vw;
    position: absolute;
    right: 0;
    top: 0;
    box-sizing: border-box;
    padding: 0 18% 0 7vw;
    font-size: 2.2vw;
    margin-top: -4vw;
    z-index: 1;

    &::after {
      content: '';
      width: 38.5vw;
      height: 7vw;
      position: absolute;
      bottom: -56.5vw;
      left: -29vw;
      background-color: $color-grey;
      opacity: 0.1; } }

  a {
    display: block;
    position: absolute;
    top: 10vw;
    left: 35vw;

    &::before {
      content: '';
      width: 22vw;
      height: 40vw;
      position: absolute;
      top: -12vw;
      left: -10vw;
      background-color: $color-dark; }

    &::after {
      content: '';
      width: 9vw;
      height: 17px;
      background-color: $color-green;
      position: absolute;
      bottom: -4vw;
      left: -19vw; }

    &:hover {
      strong {
        color: lighten($color-green, 10%); }
      h2 {
        padding-left: 10px;
        background: {
          position: right center; } } } }

  strong {
    display: block;
    width: 40vw;
    color: $color-green;
    font: 3vw/1.4 $font-title;
    position: relative;
    z-index: 1;
    transition: all 0.5s $ease; }

  h2 {
    width: 15vw;
    color: $color-green;
    font: {
      size: 2.2vw;
      weight: 600; }
    margin: 3vw 0 0 27vw;
    position: relative;
    z-index: 1;
    transition: all 0.5s $ease;
    padding-right: 50px;
    background: {
      image: url('../img/arrow-green.svg');
      repeat: no-repeat;
      size: 30px auto;
      position: 90% center; } } }
