// Form de contato
#contact-popup {
  @extend %popup;
  top: 12vw;

  &.is-active {
    @extend %popup-is-active;
    height: 100%;

    p,
    fieldset {
      opacity: 1; } }

  strong {
    position: absolute;
    top: 3.5vw;
    right: $default-margin;
    text-align: right;
    font: 3vw $font-title;
    width: 14vw;
    line-height: 1.2; }

  p {
    padding: 3vw 0 5vw 10vw;
    width: 30vw;
    font: {
      size: 2vw;
      weight: 600; }
    line-height: 1.2;
    position: relative;
    transition: opacity 0.9s $ease 0.5s;
    opacity: 0;

    &::before {
      content: '';
      width: 6vw;
      height: 4px;
      background-color: $color-grey;
      position: absolute;
      top: 4vw;
      left: 0; } }

  fieldset {
    width: 30vw;
    max-width: 380px;
    padding-left: 5vw;
    transition: opacity 0.9s $ease 0.5s;
    opacity: 0; }

  input:not([type="submit"]) {
    border: solid 3px $color-dark;
    background-color: lighten($color-green, 5%);
    width: 30vw;
    max-width: 380px;
    height: 45px;
    margin-bottom: 1vw;
    box-sizing: border-box;
    padding: 0 20px;
    color: $color-dark; }

  input[type="tel"] {
    background-color: $color-dark;
    color: $color-grey;
    max-width: 260px;

    &::placeholder {
      color: $color-grey; } }

  textarea {
    border: solid 3px $color-dark;
    background-color: lighten($color-green, 5%);
    width: 30vw;
    max-width: 380px;
    height: 7vw;
    box-sizing: border-box;
    padding: 10px 20px;
    color: $color-dark; }

  ::placeholder {
    color: $color-dark;
    opacity: 1; }

  input[type="submit"] {
    border: 0;
    background: {
      color: $color-dark;
      image: url('../img/send.svg');
      repeat: no-repeat;
      position: 85% center;
      size: 18px auto; }
    color: $color-grey;
    padding: 8px 50px 8px 16px;
    margin-top: -4px;
    transition: all 0.5s $ease;

    &:hover {
      background-position: 88% 33%; } }

  .close {
    @extend %close;
    background-color: $color-green;
    top: -2.5vw;
    right: $default-margin; } }
