// Benefícios
#benefits {
  position: relative;
  height: 100vw;

  header {
    width: 56vw;
    height: 77vw;
    box-sizing: border-box;
    padding: 10vw 0 0 $default-margin;
    position: relative;

    &::before {
      content: '';
      width: 100%;
      height: 100%;
      background-color: $color-grey;
      position: absolute;
      left: 0;
      top: 0;
      opacity: 0.1; }

    &::after {
      content: '';
      width: 70%;
      height: 60%;
      background: url('../img/about.jpg') no-repeat left center {
        size: cover; }
      position: absolute;
      left: 0;
      top: 0;
      mix-blend-mode: darken;
      opacity: 0.4; }

    & > * {
      position: relative;
      z-index: 1; }

    h2 {
      font: {
        family: $font-title;
        size: 7.5vw; }
      color: $color-dark;
      position: absolute;
      top: -4.5vw;
      right: -2vw;
      text-transform: lowercase; }

    small {
      font-size: 1.8vw;
      width: 16vw;
      display: block; }

    strong {
      font: {
        size: 4vw;
        weight: 600; }
      width: 34vw;
      display: block;
      margin-top: 3vw; } }

  figure {
    width: 42vw;
    height: 58vw;
    background: url('../img/benefits.jpg') no-repeat center center {
      size: cover; }
    position: absolute;
    top: 34vw;
    left: $default-margin;
    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.3);

    &::before {
      content: '';
      width: 9vw;
      height: 17px;
      background-color: $color-green;
      position: absolute;
      top: 0;
      left: 0; }

    &::after {
      content: '';
      width: 9vw;
      height: 17px;
      background-color: $color-green;
      position: absolute;
      bottom: 0;
      right: -22vw; } }

  ul {
    width: 44vw;
    position: absolute;
    right: 0;
    top: 0;
    box-sizing: border-box;
    padding: 10vw $default-margin 0 7vw;
    z-index: 1;

    li {
      position: relative;
      margin-bottom: 5vw;

      &::before {
        content: '';
        width: 30px;
        height: 30px;
        border: solid 6px $color-green {
          radius: 100%; }
        box-sizing: border-box;
        position: absolute;
        top: 0.5vw;
        left: -7.8vw; } }

    h3 {
      font: {
        family: $font-title;
        size: 2.4vw; }
      margin-bottom: 2vw; }

    h4 {
      font: {
        size: 1.5vw;
        weight: 300; }
      color: #62a28b;
      line-height: 1.3;
      width: 70%; } } }
