// Import partials from sass_dir
@import "slick-carousel";
@import "base";


#lionx {
  position: relative;
  background-color: $color-dark;
  min-height: 100vh;
  box-sizing: border-box;
  overflow-x: hidden;

  & > h1 {
    display: none; } }



@import "navbar";
@import "cover";
@import "about";
@import "features";
@import "benefits";
@import "demo";



.grey-background {
  display: block;
  background-color: $color-grey;
  position: relative;

  &::before {
    content: '';
    width: 100%;
    height: 100%;
    background: url('../img/about.jpg') no-repeat center {
      size: cover; }
    position: absolute;
    left: 0;
    top: 0;
    mix-blend-mode: color-dodge;
    opacity: 0.5; } }



@import "tweets";
@import "contact";
@import "form-newsletter";
@import "form-contact";
@import "form-demo";
@import "menu";



// Particles
#particles-js,
#particles2 {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  transition: all 0.5s $ease;

  canvas {
    display: block; }

  &.is-active {
    z-index: 5;
    background-color: $color-grey; } }



// Media queries
@import "media-tablet";
@import "media-smartphone";
@import "media-big-desktop";
