/*Sobre*/
#about {
  background-color: $color-grey;
  position: relative;
  padding-bottom: 20vw;

  &::before {
    content: '';
    width: 100%;
    height: 100%;
    background: url('../img/about.jpg') no-repeat center {
      size: cover; }
    position: absolute;
    left: 0;
    top: 0;
    mix-blend-mode: color-dodge;
    opacity: 0.5; }

  &::after {
    content: '';
    width: 35vw;
    height: 26vw;
    background-color: $color-beige;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 1; }

  h2 {
    padding-left: $default-margin;
    margin-top: $default-margin;
    display: inline-block;
    color: $color-dark;
    width: 35vw;
    font-size: 3vw;
    position: relative;
    z-index: 1;

    &::before {
      content: '';
      width: 16px;
      height: 100%;
      background-color: $color-beige;
      position: absolute;
      left: 0;
      top: 0; } }

  & > p {
    width: 100%;
    margin-top: $default-margin;
    color: $color-dark;
    display: inline-block;
    box-sizing: border-box;
    padding: 0 30vw $default-margin;
    font-size: 1.8vw;
    line-height: 1.5;
    position: relative;
    z-index: 1;

    &::before {
      content: '';
      width: 21vw;
      height: 4px;
      background-color: $color-green;
      position: absolute;
      left: 5vw;
      top: 10px; } }

  figure {
    width: 45vw;
    height: 28vw;
    background: url('../img/about.jpg') no-repeat center center {
      size: cover; }
    position: absolute;
    top: -8vw;
    right: $default-margin;
    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.3);
    z-index: 2;
    transition: 0.4s $ease;

    &::after {
      content: '';
      width: 17vw;
      height: 23px;
      background-color: $color-green;
      position: absolute;
      left: 18vw;
      bottom: -23px; }

    &.is-active {
      background: url('../img/crew.jpg') no-repeat center center {
        size: cover; }
      z-index: 3;
      top: -11vw;

      &::after {
        background-color: $color-grey;
        width: 2vw;
        height: 8vw;
        left: -2vw;
        bottom: 0; } } }

  .button-expand-popup {
    @extend %button-expand-popup;
    top: 26vw;
    right: $default-margin; }


  .popup {
    @extend %popup;
    top: 26vw;
    @extend %clear;
    overflow: hidden;

    &.is-active {
      @extend %popup-is-active;
      height: 62%;

      span {
        opacity: 1; } }

    strong {
      position: absolute;
      top: 22vw;
      right: $default-margin;
      text-align: right;
      font: 3vw $font-title;
      width: 14vw;
      line-height: 1.2; }

    span {
      display: block;
      width: 40%;
      box-sizing: border-box;
      padding: 5vw 0 2vw 5vw;
      float: left;
      position: relative;
      transition: opacity 0.9s $ease 0.5s;
      opacity: 0;

      &:first-of-type {
        p:first-child::before {
          content: '';
          width: 7vw;
          height: 3px;
          background-color: $color-grey;
          position: absolute;
          left: 0;
          top: -20px; }

        p:last-child::before {
          bottom: -3vw;
          height: 11vw; } }

      &:last-of-type {
        p:first-child {
          margin-top: 17vw; }

        p:last-child::before {
          bottom: 0;
          height: 6vw; } } }

    p {
      font: {
        size: 1.5vw;
        weight: 400; }
      line-height: 1.4;
      position: relative;

      &:not(:first-child) {
        margin-top: 3vw; }

      &:last-child {
        padding-left: 3vw;
        &::before {
          content: '';
          width: 1.5vw;
          background-color: $color-grey;
          position: absolute;
          left: 0; } } }

    em {
      font: {
        size: 1.7vw;
        weight: 600; } }

    .close {
      @extend %close;
      background-color: $color-grey;
      top: 32vw;
      right: $default-margin; } } }
