// Contato
#contact {
  padding: 15vw 0 8vw;
  position: relative;

  &::after {
    content: '';
    width: 35vw;
    height: 100%;
    background-color: $color-beige;
    position: absolute;
    right: 0;
    top: 0; }

  figure {
    width: 50vw;
    height: 51vw;
    position: absolute;
    top: 15vw;
    right: $default-margin;
    background: url('../img/contact.jpg') no-repeat 15% center {
      size: cover; }
    z-index: 1;
    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.3);
    transition: all 0.5s $ease;

    &::after {
      content: '';
      width: 13vw;
      height: 17px;
      background-color: $color-green;
      position: absolute;
      bottom: -17px;
      right: 0; }

    &.is-active {
      width: 40vw;
      height: 41vw;
      background: url('../img/office.jpg') no-repeat 20% center {
        size: cover; }
      z-index: 3;
      top: 11vw;

      &::before {
        content: '';
        width: 9vw;
        height: 17px;
        background-color: $color-grey;
        position: absolute;
        top: -17px;
        left: 0; }

      figcaption {
        opacity: 1;
        transition: all 0.5s $ease 0.5s; } } }

  figcaption {
    padding: 12px 12px 12px 50px;
    background: url('../img/location.svg') no-repeat 13px 15px rgba(9, 27, 30, 0.8) {
      size: 25px auto; }
    color: $color-grey;
    font-size: 0.8rem;
    display: inline-block;
    opacity: 0;

    a {
      color: $color-green; } }


  .button-expand-popup {
    @extend %button-expand-popup;
    top: 12vw;
    right: $default-margin; } }
