.form-newsletter {
  width: 23vw;
  padding-top: 60px;
  margin-left: $default-margin;
  background: url('../img/email.svg') no-repeat 0 top {
    size: 40px auto; }
  position: relative;
  z-index: 1;

  p {
    color: $color-dark;
    font: {
      size: 2vw; }
    line-height: 1.2;
    margin-bottom: 40px;
    width: 95%; }

  fieldset {
    width: 100%;
    position: relative; }

  input {
    height: 45px;
    border: solid 1px $color-dark;
    box-sizing: border-box;
    padding: 0 20px;
    color: $color-dark;

    &:not(:last-of-type) {
      width: 100%;
      font: 0.8rem $font-title;
      background-color: transparent; }

    &:nth-of-type(2) {
      margin-top: -1px;
      padding-right: 80px; }

    &[type="submit"] {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 65px;
      background: {
        color: $color-green;
        image: url('../img/arrow-send.svg');
        repeat: no-repeat;
        size: auto 70%;
        position: center 50%; }
      transition: all 0.3s $ease;

      &:hover {
        background: {
          position: 60% 50%;
          color: lighten($color-green, 10%); } } } } }
