// Menu
.menu {
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  display: flex;

  ol {
    flex: 0 1 65%;
    align-self: center;
    box-sizing: border-box;
    padding: 5vw 0 0 16vw;
    font: 5vw $font-title; }

  li {
    margin-bottom: 1vw;

    &:first-child {
      @supports (-webkit-text-stroke: 1px $color-green) {
        a {
          -webkit-text-stroke: 1px $color-green;
          -webkit-text-fill-color: $color-grey; } } }

    a {
      color: $color-green;
      transition: all 0.3s $ease;

      &:hover {
        padding-left: 10px; } } }


  & > div {
    flex: 0 1 35%;
    box-sizing: border-box;
    align-self: flex-end;
    position: relative;

    a[title*="demo"] {
      @extend %button-demo;
      margin-bottom: 70px; }

    a[title*="LinkedIn"] {
      width: 45px;
      height: 45px;
      background: {
        size: 100% auto;
        repeat: no-repeat;
        position: left top; }
      display: block;
      margin-bottom: 30px; }

    address {
      font-style: normal;
      padding-bottom: 30px; }

    small {
      font-size: 0.6rem;
      text-transform: uppercase;
      opacity: 0.7;
      padding-bottom: 30px;
      display: block; } }


  // Menu fixo
  &:first-of-type {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 6;
    transform: translateY(-110vh);
    transition: all 0.7s $ease;

    li {
      opacity: 0;

      &:nth-child(1) {
        transform: translateY(-30px);
        transition-delay: 0.8s; }
      &:nth-child(2) {
        transform: translateY(-50px);
        transition-delay: 0.7s; }
      &:nth-child(3) {
        transform: translateY(-70px);
        transition-delay: 0.6s; }
      &:nth-child(4) {
        transform: translateY(-90px);
        transition-delay: 0.5s; }
      &:nth-child(5) {
        transform: translateY(-110px);
        transition-delay: 0.4s; } }

    & > div {
      color: $color-dark;
      transform: translateY(-40vh);
      opacity: 0;

      a[title*="LinkedIn"] {
        background-image: url('../img/linkedin-green.svg'); }

      address {
        a {
          color: $color-dark; } } } }

  &.is-active {
    transform: translateY(0);

    li {
      transition: all 0.5s $ease;
      opacity: 1;
      transform: translateY(0) !important; }

    & > div {
      transition: all 1.3s $ease;
      transform: translateY(0);
      opacity: 1; } }


  // Menu do footer
  &:last-of-type {
    margin-top: 25vw;
    position: relative;
    z-index: 1;

    li {
      a {
        color: $color-grey; }

      &:first-child {
        @supports (-webkit-text-stroke: 1px $color-green) {
          a {
            -webkit-text-stroke: 1px $color-green;
            -webkit-text-fill-color: $color-dark; } } } }

    & > div {
      a[title*="LinkedIn"] {
        background-image: url('../img/linkedin-grey.svg'); }

      address {
        a {
          color: $color-grey; } } } } }
