#form-demo {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -15;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  visibility: hidden;

  &::before {
    content: '';
    width: 100%;
    height: 100%;
    background-color: $color-dark;
    opacity: 0.8;
    position: absolute;
    top: 0;
    left: 0; }

  &.is-active {
    opacity: 1;
    z-index: 15;
    visibility: visible;

    form {
      transition: all 0.6s $ease;
      opacity: 1;
      transform: translateY(0); } }


  form {
    flex: 0 0 700px;
    box-sizing: border-box;
    padding: 50px 160px;
    background-color: #404040;
    position: relative;
    z-index: 1;
    transform: translateY(200px);
    opacity: 0;

    &::before {
      content: '';
      width: 100%;
      height: 100%;
      background: url('../img/charts.jpg') no-repeat 50% 50% {
        size: contain; }
      position: absolute;
      top: 0;
      left: 0;
      mix-blend-mode: color; } }

  strong {
    font: 1.8rem $font-title;
    color: $color-green;
    padding-bottom: 20px;
    margin-bottom: 20px;
    position: relative;
    display: block;

    &::after {
      content: '';
      width: 70px;
      height: 4px;
      background-color: $color-light;
      position: absolute;
      left: 0;
      bottom: 0; } }

  p {
    font: {
      size: 1.4rem;
      weight: 600; }
    color: $color-green;
    margin-bottom: 20px;
    position: relative; }

  fieldset {
    position: relative; }


  input {
    &:not([type="submit"]) {
      border: solid 3px $color-green;
      background-color: white;
      width: 100%;
      height: 45px;
      margin-bottom: 1vw;
      box-sizing: border-box;
      padding: 0 20px;
      color: $color-green; }

    &[type="tel"] {
      background-color: $color-green;
      color: $color-grey;
      max-width: 260px;

      &::placeholder {
        color: white; } }

    &::placeholder {
      color: $color-green; }

    &[type="submit"] {
      font: 0.8rem $font-title;
      height: 45px;
      color: $color-green;
      padding: 0 70px 0 20px;
      border: solid 1px $color-green;
      background: {
        image: url(../img/icon-graph.svg);
        repeat: no-repeat;
        position: 94% center;
        size: 43px auto;
        color: transparent; }
      transition: all 0.2s;

      &:hover {
        color: lighten($color-green, 20%); } } }


  .close {
    @extend %close;
    background-color: $color-green;
    top: -2.5vw;
    right: 4%; } }

