// Smartphone styles
@include for-size(phone-only) {

  #lionx {
    overflow: hidden; }

  .navbar {
    figure {
      margin-left: 25%; }

    & > div {
      position: absolute;
      top: 8px;
      left: 7vw;

      button {
        overflow: hidden;
        text-indent: 999px;
        width: 7vw;

        &:last-child {
          display: none; } } } }

  #cover {
    &::before {
      background-position: center; }

    @keyframes scroll-down {
      from {
        bottom: -100px; }
      to {
        bottom: -110px; } }

    figure {
      width: 80vw;
      height: 100%;
      top: 0;
      margin-top: 0;
      background-position: left -40vw;
      background-size: 200% auto; }

    h2 {
      font-size: 8.5vw; } }

  #about {
    display: flex;
    flex-wrap: wrap;

    &::after {
      width: 65vw;
      height: 50vw; }

    figure {
      order: 1;
      position: relative;
      top: 0;
      right: 0;
      width: 86vw;
      height: 60vw;
      margin: -25vw auto 0;

      &::after {
        width: 37vw;
        height: 20px;
        left: 28vw;
        bottom: -20px; }

      &.is-active {
        position: fixed;
        width: 92vw;
        top: 0;
        right: 0;
        height: 20%;
        margin: 0;
        z-index: 11;

        &::after {
          display: none; } } }

    h2 {
      order: 2;
      width: 100%;
      font-size: 5.5vw;
      padding-left: 18%;
      padding-right: 18%;
      margin-top: 20%;
      line-height: 1.4; }

    & > p {
      order: 3;
      padding: 10vw 10vw 8% 18vw;
      font-size: 4.4vw;

      &::before {
        left: 18vw;
        top: 0; } }

    .button-expand-popup {
      order: 4;
      position: relative;
      top: 0;
      right: 0;
      width: auto;
      height: auto;
      background-image: none;
      font-size: 0.8rem;
      margin-left: 18vw;
      padding: 10px 30px;

      span {
        text-align: left;
        float: none;
        width: auto; } }


    .popup {
      height: 100vh;
      position: fixed;
      width: 100%;
      top: initial;
      right: 0;
      bottom: -110vh;
      padding: 30vh $default-margin 40px;
      overflow-y: auto;
      z-index: -1;
      transition: bottom 0.7s $ease;

      &.is-active {
        bottom: 0;
        z-index: 10;
        height: 100vh;
        width: 100%; }

      strong {
        position: static;
        display: block;
        width: auto;
        text-align: left;
        font-size: 1.2rem;
        padding: 0 0 30px; }

      span {
        width: 100%;
        float: initial;
        padding: 0;

        &:last-of-type {
          p:first-child {
            margin-top: 2rem; }
          p:last-child::before {
            display: none; } } }

      p {
        font-size: 1rem;

        &:not(:first-child) {
          margin-top: 2rem; }
        &:last-child {
          padding-left: 0;
          &::before {
            display: none; } } }

      em {
        font-size: 1.1rem; }

      .close {
        position: fixed;
        top: 25vh;
        right: 15px;
        width: 40px;
        height: 40px; } } }



  #features {
    ol {
      margin-top: 0;

      &::after {
        display: none; }

      li {
        background-image: none !important;
        width: auto;
        height: 40px;
        margin-right: 25px;

        &::before {
          display: none; }
        &::after {
          display: none; }

        &.is-active {
          transform: scale(1);
          margin-right: 25px;
          box-shadow: none;

          button {
            &::after {
              height: 3px; } } } }

      button {
        &::before {
          display: none; }
        &::after {
          width: 100%;
          height: 3px; } }

      span {
        font-size: 1rem;
        margin: 0; } }

    article {
      padding: 0 8vw;
      margin: 60vw 0 23vw;

      &::after {
        display: none; }

      &::before {
        width: 84vw;
        height: 35vw;
        right: 50%;
        top: -45vw;
        opacity: 1;
        mix-blend-mode: unset;
        margin-right: -42%; }

      h2 {
        width: auto;
        font-size: 5.6vw;

        &::after {
          width: 92vw;
          left: -8vw;
          top: 30vw;
          height: 20vw; } }

      h3 {
        font-size: 4.5vw;
        border-bottom: 0;
        padding-left: 4vw; }

      .features-list {
        position: absolute;
        top: 34vw;
        right: initial;
        width: 84vw;

        &::after {
          width: 100%;
          height: 18vw;
          top: -3vw; } }

      .feature-item {
        text-align: left; }

      p {
        width: auto;
        margin: 34.5vw 0 8vw;
        font-size: 1rem; } }

    .tab-ia {
      h2 {
        width: 80%; } } }


  #benefits {
    height: auto;

    header {
      width: 100%;
      height: auto;

      &::after {
        width: 100%;
        height: 310%; }

      h2 {
        display: none; }

      small {
        font-size: 6vw;
        width: 60%; }

      strong {
        font-size: 7vw;
        width: 84vw; } }

    figure {
      width: 20vw;
      height: 155vw;
      top: 54vw;
      left: 0;
      background-position: 43% center;

      &::after {
        width: 10vw;
        height: 12px;
        bottom: 0;
        right: 0; } }

    ul {
      width: 100%;
      top: 55vw;
      position: static;
      padding: 15vw 8% 10vw 32vw;
      background-color: #1e2e31;

      li {
        margin-bottom: 8vw;

        &::before {
          width: 15px;
          height: 15px;
          border: solid 4px $color-green;
          left: -14.2vw; } }

      h3 {
        font-size: 4.4vw;
        margin-bottom: 4vw; }

      h4 {
        font-size: 0.9rem;
        width: 100%; } } }


  #demo {
    height: auto;
    padding: 20vw 0 10vw;
    margin: 0;

    &::after {
      display: none; }

    small {
      position: static;
      padding: 0 0 0 33vw;
      width: 65vw;
      font-size: 4.2vw; }

    a {
      position: static;
      padding-left: 10vw;
      margin: 30px 0;

      &::before {
        width: 15vw;
        height: 60vw;
        top: 65px;
        left: 0; }

      strong {
        width: 50vw;
        font-size: 7vw; } }

    h2 {
      width: 30vw;
      font-size: 5.2vw;
      margin: 7vw 0 0 22vw; } }


  #tweets {
    margin-top: 5vw;
    display: block;

    &::after {
      display: none; }

    strong {
      display: block;
      font-size: 3.5vw; }

    .carousel {
      padding-left: 14%;

      button {
        left: 0;
        height: 80px;
        width: 14%;

        &.slick-next {
          top: calc(10vw + 80px); } } } }

  #contact {
    &::after {
      display: none; }

    .button-expand-popup {
      position: static; }

    figure {
      position: static;
      width: 84vw;
      margin: 40px auto 0;

      &.is-active {
        position: fixed;
        width: 92vw;
        top: 0;
        right: 0;
        height: 20%;
        margin: 0;
        z-index: 11;

        &::after {
          display: none; } } }

    .button-expand-popup {
      position: absolute;
      top: 131vw;
      right: initial;
      left: 8%;
      width: auto;
      height: auto;
      background-image: none;
      font-size: 0.8rem;
      padding: 20px 30px;

      span {
        text-align: left;
        float: none;
        width: auto; } } }

  .form-newsletter {
    width: 100%;
    margin: 0;
    padding: 60px 7% 0;
    box-sizing: border-box;
    background-position: $default-margin top;

    p {
      font-size: 5.4vw; } }


  #contact-popup {
    height: 100vh;
    position: fixed;
    width: 100%;
    top: initial;
    right: 0;
    bottom: -110vh;
    padding: 30vh $default-margin 40px;
    overflow-y: auto;
    z-index: -1;
    transition: bottom 0.7s $ease;

    &.is-active {
      bottom: 0;
      width: 100%;
      z-index: 10; }

    strong {
      position: static;
      display: block;
      width: auto;
      text-align: left;
      font-size: 1.2rem;
      padding: 0 0 30px; }

    fieldset {
      width: 100%;
      max-width: initial;
      padding-left: 0; }

    p {
      padding: 0 0 30px;
      width: 100%;
      font-size: 1.2rem;

      &::before {
        top: -15px; } }

    input:not([type="submit"]) {
      width: 100%;
      max-width: initial;
      height: 50px;
      margin-bottom: 15px; }

    textarea {
      width: 100%;
      max-width: initial;
      height: 100px; }

    input[type="submit"] {
      padding: 14px 60px 14px 26px; }

    .close {
      position: fixed;
      top: 25vh;
      right: 15px;
      width: 40px;
      height: 40px;
      background-color: $color-light; } }


  .menu {
    flex-wrap: wrap;
    height: auto;


    ol {
      flex: 0 1 100%;
      padding: 30vw 0 0 8vw;
      font: 7vw $font-title; }

    li {
      margin-bottom: 4vw; }

    & > div {
      flex: 0 1 100%;
      padding: 0 8%; }

    &:first-of-type {
      max-height: 100vh;

      & > div {
        address,
        small {
          display: none; } } }

    &:last-of-type {
      margin-top: 0; } }



  #form-demo {
    form {
      flex: 0 0 92%;
      padding: 50px 10%; }

    input {
      &[type="tel"] {
        max-width: 100%; } }

    .close {
      width: 45px;
      height: 45px; } } }
