
// fonts
@import url('https://fonts.googleapis.com/css2?family=Krona+One&family=Work+Sans:wght@300;400;600&display=swap');

// vars
$font-title:  'Krona One', sans-serif;
$font-text:   'Work Sans', sans-serif;

$color-dark:    #091b1e;
$color-green:   #18d174;
$color-light:   #c7e8dc;
$color-grey:    #e5e5e5;
$color-beige:   #ffecb3;

$cursor: url('../img/cursor.svg'), auto;
$ease: cubic-bezier(.5,0,0,1);
$default-margin: 8%;

// resets
html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, a, blockquote, pre, abbr, address, cite, code, del, dfn, em, img, ins, kbd, q, samp, small, strong, sub, sup, var, b, i, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, figure, footer, header, hgroup, nav, section, menu, time, mark, audio, video, canvas {
  margin:     0;
  padding:    0;
  border:     0; }

html, body {
  min-height:               100%;
  -ms-text-size-adjust:     100%;
  -webkit-text-size-adjust: 100%; }

body, input, textarea, button {
  font:                     100% $font-text;
  color:                    $color-light;
  -webkit-font-smoothing:   antialiased;
  outline:                  none;
  cursor: $cursor; }

a {
  text-decoration:   none;
  cursor:            $cursor; }
ul, ol, dl {
  list-style:        none; }
button {
  padding:           0;
  border:            0;
  background-color:  transparent;
  cursor:            $cursor; }
em {
  font-style:        normal; }
strong, h1, h2, h3, h4, h5, h6 {
  font-weight:       normal; }


// selection
::selection {
  background:       $color-grey;
  color:            $color-green; }


// placeholder
@mixin placeholder($color) {
  &::-webkit-input-placeholder {
    color: #{$color}; }
  &:-ms-input-placeholder {
    color: #{$color}; }
  &::placeholder {
    color: #{$color}; } }


// scrollbars webkit
::-webkit-scrollbar {
  width: 3px; }
::-webkit-scrollbar-track {
  background-color: $color-dark; }
::-webkit-scrollbar-thumb {
  background-color: $color-green; }
::-webkit-scrollbar-thumb:hover {
  background-color: lighten($color-green, 15%); }


%clear {
  &:after {
    height:             0;
    clear:              both;
    content:            ".";
    display:            block;
    font-size:          0;
    visibility:         hidden;
    position:           initial; } }


// Botão demo
%button-demo {
  font: {
    family: $font-title;
    size: 1rem; }
  color: $color-green;
  padding: 16px 70px 20px 0;
  position: relative;
  display: inline-block;
  transition: all 0.3s $ease 0.2s;
  background: {
    image: url('../img/icon-graph.svg');
    repeat: no-repeat;
    position: right center;
    size: 43px auto; }

  &::after {
    content: '';
    width: 100%;
    height: 0;
    border: solid 2px $color-green;
    position: absolute;
    bottom: 0;
    left: 0;
    transition: all 0.4s $ease; }

  &:hover {
    padding: 16px 90px 20px 30px;
    background-position: 92% center;
    transition: all 0.5s $ease;
    &::after {
      height: 100%;
      border-size: 3px;
      transition: all 0.8s $ease 0.2s; } } }


// Botão de expandir popup
%button-expand-popup {
  width: 13vw;
  height: 6vw;
  display: block;
  box-sizing: border-box;
  padding: 15px;
  background: {
    color: $color-green;
    image: url('../img/corner-arrow.svg');
    repeat: no-repeat;
    position: 4px 92%;
    size: 10px auto; }
  color: $color-dark;
  font: {
    family: $font-title;
    size: 1vw; }
  position: absolute;
  z-index: 2;
  transition: all 0.5s $ease 0.2s;

  &:hover {
    background: {
      color: lighten($color-green, 10%);
      position: 0 100%; }
    span {
      padding-right: 5px; } }

  span {
    display: block;
    text-align: right;
    float: right;
    width: 50%;
    transition: all 0.5s $ease; } }


// Botão fechar popup
%close {
  width: 5vw;
  height: 5vw;
  background: {
    image: url('../img/close.svg');
    repeat: no-repeat;
    position: center 50%;
    size: 50% auto; }
  border-radius: 100%;
  position: absolute; }


// Popup
%popup {
  width: 13vw;
  height: 6vw;
  box-sizing: border-box;
  padding-right: $default-margin;
  background-color: rgba(#18d174, 0.97);
  color: $color-dark;
  position: absolute;
  right: $default-margin;
  z-index: 0;
  transition: width 0.7s $ease, height 0.7s $ease, top 0.7s $ease, right 0.7s $ease;
  opacity: 0; }

%popup-is-active {
  width: 92%;
  right: 0;
  top: 0;
  z-index: 2;
  opacity: 1; }


// Animate on scroll
.slide-in {
  opacity: 0; }

.align-left {
  transform: translate(-30%, 0); }
.align-right {
  transform: translate(30%, 0); }
.align-up {
  transform: translate(0, 50px); }
.fade {
  opacity: 0.3;
  transform: translate(0, 30px); }

.slide-actived {
  opacity: 1;
  transform: translate(0, 0);
  transition: all 1s $ease .4s; }



// Media queries
@mixin for-size($size) {
  @if $size == phone-only {
    @media (max-width: 599px) {
      @content; } }
  @else if $size == tablet-portrait-up {
    @media (min-width: 600px) and (orientation: portrait) and (max-width: 1199px) {
      @content; } }
  @else if $size == tablet-landscape-up {
    @media (min-width: 900px) and (orientation: landscape) and (max-width: 1199px) {
      @content; } }
  @else if $size == desktop-up {
    @media (min-width: 1200px) {
      @content; } }
  @else if $size == big-desktop-up {
    @media (min-width: 1800px) {
      @content; } } }
